import { createSelector } from "reselect";
var selectShop = function (state) { return state.shop; };
export var selectCollections = createSelector(selectShop, function (shop) { return shop.collections; });
export var selectCollection = function (collectionUrlParam) {
    return createSelector(selectCollections, function (collections) {
        return collections[collectionUrlParam];
    });
};
export var selectCollectionForPreview = createSelector(selectCollections, function (collections) { return Object.keys(collections).map(function (key) { return collections[key]; }); });
export var selectIsCollectionFetching = createSelector(selectShop, function (shop) { return shop.isFetching; });
