var TOGGLE_CART_HIDDEN = "TOGGLE_CART_HIDDEN";
var ADD_ITEM = "ADD_ITEM";
var REMOVE_ITEM = "REMOVE_ITEM";
var CLEAR_ITEM_FROM_CART = "CLEAR_ITEM_FROM_CART";
export var toggleCartHidden = function () { return ({
    type: "TOGGLE_CART_HIDDEN"
}); };
export var addItem = function (cartItem) { return ({
    type: "ADD_ITEM",
    payload: cartItem
}); };
export var removeItem = function (cartItem) { return ({
    type: "REMOVE_ITEM",
    payload: cartItem
}); };
export var clearItemFromCart = function (cartItem) { return ({
    type: "CLEAR_ITEM_FROM_CART",
    payload: cartItem
}); };
