import { firestore } from "$firebase/utils";
var FETCH_COLLECTIONS_START = "FETCH_COLLECTIONS_START";
var FETCH_COLLECTIONS_SUCCESS = "FETCH_COLLECTIONS_SUCCESS";
var FETCH_COLLECTIONS_FAILURE = "FETCH_COLLECTIONS_FAILURE";
export var fetchCollectionsStart = function () { return ({
    type: "FETCH_COLLECTIONS_START"
}); };
export var fetchCollectionsSuccess = function (collections) { return ({
    type: "FETCH_COLLECTIONS_SUCCESS",
    payload: collections
}); };
export var fetchCollectionsFailure = function (errorMsg) { return ({
    type: "FETCH_COLLECTIONS_FAILURE",
    payload: errorMsg
}); };
export var fetchCollectionsStartAsync = function () { return function (dispatch) {
    var collectionsRef = firestore.collection("collections");
    dispatch(fetchCollectionsStart());
    collectionsRef
        .get()
        .then(function (snapshot) {
        var transformedCollections = snapshot.docs.map(function (docSnapshot) {
            var _a = docSnapshot.data(), title = _a.title, items = _a.items;
            return {
                routeName: encodeURI(title.toLowerCase()),
                id: docSnapshot.id,
                title: title,
                items: items
            };
        });
        var ret = transformedCollections.reduce(function (prev, c) {
            prev[c.title] = c;
            return prev;
        }, {});
        dispatch(fetchCollectionsSuccess(ret));
    })
        .catch(function (error) { return dispatch(fetchCollectionsFailure(error.message)); });
}; };
