import { createStore, applyMiddleware, compose, combineReducers } from "redux";
import { persistStore, persistReducer } from "redux-persist";
import localStorage from "redux-persist/lib/storage";
import logger from "redux-logger";
import thunk from "redux-thunk";
import userReducer from "./user/reducer";
import cartReducer from "./cart/reducer";
import directoryReducer from "./directory/reducer";
import shopReducer from "./shop/reducer";
import { isProduction } from "Config";
var rootReducer = combineReducers({
    user: userReducer,
    cart: cartReducer,
    directory: directoryReducer,
    shop: shopReducer
});
var persistConnfig = {
    key: "root",
    storage: localStorage,
    whitelist: ["cart"]
};
var composeEnhancers = (!isProduction && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) || compose;
var middlewares = [!isProduction && logger, thunk].filter(Boolean);
export var store = createStore(persistReducer(persistConnfig, rootReducer), composeEnhancers(applyMiddleware.apply(void 0, middlewares)));
export var persistor = persistStore(store);
