// Todo: Purpose unclear, can't see fundamental performance difference
import { createSelector } from "reselect";
var selectCart = function (state) { return state.cart; };
export var selectCartHidden = createSelector([selectCart], function (cart) { return cart.hidden; });
export var selectCartItems = createSelector([selectCart], function (cart) { return cart.cartItems; });
export var selectCartItemsCount = createSelector([selectCartItems], function (cartItems) { return cartItems.reduce(function (cnt, item) { return cnt + item.quantity; }, 0); });
export var selectCartTotal = createSelector([selectCartItems], function (cartItems) { return cartItems.reduce(function (acc, item) { return acc + item.quantity * item.price; }, 0); });
