import React from "react";
import "./index.scss";
import CustomButton from "../CustomButton";
import CartItem from "../CartItem";
import { selectCartItems } from "$redux/cart/selector";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import { PUBLIC_URL } from "Config";
import { toggleCartHidden } from "$redux/cart/action";
var CartDropdown = function (_a) {
    var cartItems = _a.cartItems, toggleHidden = _a.toggleHidden;
    var H = useHistory();
    return (<div className="cart-dropdown">
      <div className="cart-items">
        {cartItems.length ? (cartItems.map(function (cartItem) { return (<CartItem key={cartItem.id} item={cartItem}/>); })) : (<div className="empty-message">Your cart is empty</div>)}
      </div>

      <CustomButton onClick={function () {
        H.push(PUBLIC_URL + "/checkout");
        toggleHidden();
    }}>
        GO TO CHECKOUT
      </CustomButton>
    </div>);
};
var mapStateToProps = createStructuredSelector({
    cartItems: selectCartItems
});
var mapDispatchToProps = function (dispatch) { return ({
    toggleHidden: function () { return dispatch(toggleCartHidden()); }
}); };
export default connect(mapStateToProps, mapDispatchToProps)(CartDropdown);
