import React from "react";
import CollectionPreview from "../CollectionPreview";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import "./index.scss";
import { selectCollectionForPreview } from "$redux/shop/selector";
var CollectionOverview = function (_a) {
    var collections = _a.collections;
    return (<div className="collection-overview">
      {collections.map(function (c) { return (<CollectionPreview key={c.id} collection={c}/>); })}
    </div>);
};
var mapStateToProps = createStructuredSelector({
    collections: selectCollectionForPreview
});
export default connect(mapStateToProps)(CollectionOverview);
