import React from "react";
import StripeCheckout from "react-stripe-checkout";
import { StripePublishableKey } from "Config";
var StripeCheckoutButton = function (_a) {
    var price = _a.price;
    var priceForStripe = price * 100;
    var onToken = function (token) {
        alert("Payment Successful");
    };
    return (<StripeCheckout token={onToken} stripeKey={StripePublishableKey} label="Pay Now" name="E-Commerce!" billingAddress shippingAddress description={"Your total is $" + price} amount={priceForStripe} panelLabel="Pay Now"/>);
};
export default StripeCheckoutButton;
